

.container {
    margin-bottom: 0px;
    padding-bottom: 5%;
    background: url(./SVG/aboutMeBG.svg);
    background-repeat: no-repeat;
    background-size: 100% 100%;
}

.container h1{
    font-family: 'Courier New', Courier, monospace;
    font-size: 3vw;
    text-align: left;
}

.aboutMe {
    padding: 15vw 10vw 0 15vw;
}

.aboutMe img {
    width: 25vw;
    border-radius: 50%;
    border: 10px solid white;
}

#secondColumn {
    color: rgb(0, 0, 0);
    font-family: 'Courier New', Courier, monospace;
    font-size: 1.5vw;
    padding-left: 2%;
    text-align: justify;
}

@media screen and (max-width: 480px) {
    .container {
        margin-top: -5px;
    }

    .aboutMe td {
        display: block;
    }

    .aboutMe {
        padding: 10vw 5vw 0vw 5vw;
    }

    .aboutMe img {
        width: 60vw;
        margin-top: 40vh;
    }

    .container h1{
        font-size: 7vw;
        text-align: left;
    }

    #secondColumn {
        font-size: 4vw;
        text-align: center;
    }
}