

.navBar {
  margin-left: 40%;
  position: sticky;
  top: 0;
  font-size: 1.1vw;
  font-family: Quicksand;
}

.navBar ul {
  padding-top: 1.5%;
  display: flex;
  list-style: none;
  justify-content: space-around;
}

.navBar li:hover {
  text-shadow: 0 0 20px #fff,
   0 0 30px #ff4da6,
    0 0 40px #ff4da6;
  font-size: 1.2vw;
  font-weight: bold;
  cursor: pointer;
  transition: 0.1s;
}

.scrolledNavBar {
  margin-top: 0%;
  position: sticky;
  top: 0;
  font-size: 1.1vw;
  padding: 1% 1% 1% 1%;
  font-family: Quicksand;
  background-color: rgb(255, 255, 255);
}

.scrolledNavBar ul {
  display: flex;
  list-style: none;
  justify-content: space-around;
}

.scrolledNavBar li:hover {
  text-shadow: 0 0 20px #fff,
   0 0 30px #ff4da6,
    0 0 40px #ff4da6;
  font-size: 1.2vw;
  font-weight: bold;
  cursor: pointer;
  transition: 0.1s;
}

#name {
  font-family: 'Courier New', Courier, monospace;
  padding-right: 20%;
  font-size: 1.5vw;
  font-weight: bold;
}