

.typeWriter {
    margin-top: 25%;
    height: 35vh;
}

.typeWriter h1 {
    font-size: 2vw;
    font-family: 'Courier New', Courier, monospace;
    text-align: left;
    margin-left: 55%;
}

@media screen and (max-width: 480px){
    #salutation {
        font-size: 7vw;
    }

    .typeWriter h1 {
        font-size: 5.6vw;
        font-family: 'Courier New', Courier, monospace;
        text-align: left;
        margin-left: 8%;
        margin-right: 8%;
    }
}
