@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=STIX+Two+Math&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', 'Quicksand', 'Open Sans',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App h1 {
  text-align: center;
}

body {
  background: url(/portfolio/static/media/drawing.4d524551.svg);
  background-repeat: no-repeat;
  background-size: 100vw 100vh;
}

.footer {
  margin-top: 0%;
  margin-bottom: 0%;
  background: url(/portfolio/static/media/footer.b1e92bae.svg);
  height: 20vh;
  color: aqua;
  background-color: #F7C9B8;
}

@media screen and (max-width: 480px) {
  body {
    background: url(/portfolio/static/media/mobileHome.3347fac4.svg);
    background-repeat: no-repeat;
    background-size: 100vw 100vh;
  }
}



.typeWriter {
    margin-top: 25%;
    height: 35vh;
}

.typeWriter h1 {
    font-size: 2vw;
    font-family: 'Courier New', Courier, monospace;
    text-align: left;
    margin-left: 55%;
}

@media screen and (max-width: 480px){
    #salutation {
        font-size: 7vw;
    }

    .typeWriter h1 {
        font-size: 5.6vw;
        font-family: 'Courier New', Courier, monospace;
        text-align: left;
        margin-left: 8%;
        margin-right: 8%;
    }
}


#projectsHeader {
    margin-top: 5%;
    font-family: 'Courier New', Courier, monospace;
    font-size: 3vw; 
}

.projectsContainer {
    border: 1px solid #EFF0FE;
    margin-top: 0;
    background: url(/portfolio/static/media/projectsBG.2c048963.svg);
    background-repeat: no-repeat;
    background-color: #EFF0FE;
}

.CardsContainer {
    display: flex;
    align-items: stretch;
    flex-wrap: wrap;
    margin-left: 8%;
    margin-right: 8%;
    margin-bottom: 10%;
    justify-content: space-around;
}

.Card {
    width: 23vw;
    margin-top: 5%;
    margin-right: 2%;
    background-color: #d4e5e6;
    border-radius: 8% 8% 8% 8%;
}

.Card img {
    border-radius: 8% 8% 0 0;
    width: 100%;
}

.Card h2 {
    font-family: Quicksand;
    text-align: center;
}

.Card h3 {
    font-family: Quicksand;
    text-align: center;
}

.Card h4 {
    font-family: Quicksand;
    text-align: center;
}

.Card:hover {
    box-shadow: 20px 20px 10px #888888;
}


@media screen and (max-width: 480px) {
    .CardsContainer {
        display: block;
    }

    .Card {
        width: 80vw;
        padding-bottom: 5%;
        background-color: #d4e5e6;
        border-radius: 8% 8% 8% 8%;
    }

    #projectsHeader {
        font-size: 7vw; 
    }
}



.container {
    margin-bottom: 0px;
    padding-bottom: 5%;
    background: url(/portfolio/static/media/aboutMeBG.37d5ee04.svg);
    background-repeat: no-repeat;
    background-size: 100% 100%;
}

.container h1{
    font-family: 'Courier New', Courier, monospace;
    font-size: 3vw;
    text-align: left;
}

.aboutMe {
    padding: 15vw 10vw 0 15vw;
}

.aboutMe img {
    width: 25vw;
    border-radius: 50%;
    border: 10px solid white;
}

#secondColumn {
    color: rgb(0, 0, 0);
    font-family: 'Courier New', Courier, monospace;
    font-size: 1.5vw;
    padding-left: 2%;
    text-align: justify;
}

@media screen and (max-width: 480px) {
    .container {
        margin-top: -5px;
    }

    .aboutMe td {
        display: block;
    }

    .aboutMe {
        padding: 10vw 5vw 0vw 5vw;
    }

    .aboutMe img {
        width: 60vw;
        margin-top: 40vh;
    }

    .container h1{
        font-size: 7vw;
        text-align: left;
    }

    #secondColumn {
        font-size: 4vw;
        text-align: center;
    }
}
#educationContainer {
    padding-top: 15vh;
    padding-bottom: 15vh;
    border-top: 1px solid #f1aaa1ff;
    background: url(/portfolio/static/media/Education.4514ecc3.svg);
    background-size: 100% 100%;
    background-repeat: no-repeat;
}

#educationHeader {
    font-family: 'Courier New', Courier, monospace;
    font-size: 3vw;
}

.educationCard {
    margin-top: 2.5%;
    font-family: Quicksand;
}

.educationCard img {
    border-radius: 50%;
    width: 10vw;
}

.educationTable {
    font-family: 'Courier New', Courier, monospace;
    /* background: rgb(220, 241, 220); */
    padding: 1% 3% 1% 3%;
    /* border-radius: 10px 100px 10px 100px;  */
    width: 50vw;
    margin-left: 5vw;
}

.educationDetails {
    padding-left: 4%;
    width: 100%;
}

@media screen and (max-width: 480px) {
    #educationContainer {
        padding-top: 10vh;
        padding-bottom: 10vh;
        background: none;
    }  

    .educationTable {
        width: 90vw;
    }

    #educationHeader {
        font-family: 'Courier New', Courier, monospace;
        font-size: 7vw;
    }

    .educationDetails h2 {
        font-size: 3.5vw;
    }

    .educationDetails h3 {
        font-size: 3vw;
    }

    .educationDetails h4 {
        font-size: 3vw;
    }

    .educationTable {
        margin-top: 5%;
        background: rgb(220, 241, 220);
        border-radius: 10px 50px 10px 50px; 
    }
}


.contactContainer {
    background: #f7c9b7;
    padding-top: 5%;
    padding-bottom: 5%;
    margin: 0 auto;
}

#contactHeader {
    font-family: 'Courier New', Courier, monospace;
    font-size: 3vw;
    margin-bottom: 5%;
}

.contactDetails {
    margin: 0 auto;
}

.circle {
    vertical-align: middle;
    margin-right: 2vw;
    height: 5vw;
    width: 5vw;
    background-color: white;
    border-radius: 50%;
    display: inline-block;    
}

.circle img {
    width: 50%;
    margin-top: 25%;
}

#mailIds {
    font-family: "QuickSand";
    font-style: italic;
    color: "black";
}

@media screen and (max-width: 480px) {
    #contactHeader {
        font-size: 7vw;
    }

    #mailIds {
        font-size: 4vw;
        margin-bottom: 8%;
    }

    .circle {
        height: 12vw;
        width: 12vw;
    }
}


.navBar {
  margin-left: 40%;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  font-size: 1.1vw;
  font-family: Quicksand;
}

.navBar ul {
  padding-top: 1.5%;
  display: flex;
  list-style: none;
  justify-content: space-around;
}

.navBar li:hover {
  text-shadow: 0 0 20px #fff,
   0 0 30px #ff4da6,
    0 0 40px #ff4da6;
  font-size: 1.2vw;
  font-weight: bold;
  cursor: pointer;
  transition: 0.1s;
}

.scrolledNavBar {
  margin-top: 0%;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  font-size: 1.1vw;
  padding: 1% 1% 1% 1%;
  font-family: Quicksand;
  background-color: rgb(255, 255, 255);
}

.scrolledNavBar ul {
  display: flex;
  list-style: none;
  justify-content: space-around;
}

.scrolledNavBar li:hover {
  text-shadow: 0 0 20px #fff,
   0 0 30px #ff4da6,
    0 0 40px #ff4da6;
  font-size: 1.2vw;
  font-weight: bold;
  cursor: pointer;
  transition: 0.1s;
}

#name {
  font-family: 'Courier New', Courier, monospace;
  padding-right: 20%;
  font-size: 1.5vw;
  font-weight: bold;
}
