.contactContainer {
    background: #f7c9b7;
    padding-top: 5%;
    padding-bottom: 5%;
    margin: 0 auto;
}

#contactHeader {
    font-family: 'Courier New', Courier, monospace;
    font-size: 3vw;
    margin-bottom: 5%;
}

.contactDetails {
    margin: 0 auto;
}

.circle {
    vertical-align: middle;
    margin-right: 2vw;
    height: 5vw;
    width: 5vw;
    background-color: white;
    border-radius: 50%;
    display: inline-block;    
}

.circle img {
    width: 50%;
    margin-top: 25%;
}

#mailIds {
    font-family: "QuickSand";
    font-style: italic;
    color: "black";
}

@media screen and (max-width: 480px) {
    #contactHeader {
        font-size: 7vw;
    }

    #mailIds {
        font-size: 4vw;
        margin-bottom: 8%;
    }

    .circle {
        height: 12vw;
        width: 12vw;
    }
}