
#projectsHeader {
    margin-top: 5%;
    font-family: 'Courier New', Courier, monospace;
    font-size: 3vw; 
}

.projectsContainer {
    border: 1px solid #EFF0FE;
    margin-top: 0;
    background: url(./SVG/projectsBG.svg);
    background-repeat: no-repeat;
    background-color: #EFF0FE;
}

.CardsContainer {
    display: flex;
    align-items: stretch;
    flex-wrap: wrap;
    margin-left: 8%;
    margin-right: 8%;
    margin-bottom: 10%;
    justify-content: space-around;
}

.Card {
    width: 23vw;
    margin-top: 5%;
    margin-right: 2%;
    background-color: #d4e5e6;
    border-radius: 8% 8% 8% 8%;
}

.Card img {
    border-radius: 8% 8% 0 0;
    width: 100%;
}

.Card h2 {
    font-family: Quicksand;
    text-align: center;
}

.Card h3 {
    font-family: Quicksand;
    text-align: center;
}

.Card h4 {
    font-family: Quicksand;
    text-align: center;
}

.Card:hover {
    box-shadow: 20px 20px 10px #888888;
}


@media screen and (max-width: 480px) {
    .CardsContainer {
        display: block;
    }

    .Card {
        width: 80vw;
        padding-bottom: 5%;
        background-color: #d4e5e6;
        border-radius: 8% 8% 8% 8%;
    }

    #projectsHeader {
        font-size: 7vw; 
    }
}
