#educationContainer {
    padding-top: 15vh;
    padding-bottom: 15vh;
    border-top: 1px solid #f1aaa1ff;
    background: url(./SVG/Education.svg);
    background-size: 100% 100%;
    background-repeat: no-repeat;
}

#educationHeader {
    font-family: 'Courier New', Courier, monospace;
    font-size: 3vw;
}

.educationCard {
    margin-top: 2.5%;
    font-family: Quicksand;
}

.educationCard img {
    border-radius: 50%;
    width: 10vw;
}

.educationTable {
    font-family: 'Courier New', Courier, monospace;
    /* background: rgb(220, 241, 220); */
    padding: 1% 3% 1% 3%;
    /* border-radius: 10px 100px 10px 100px;  */
    width: 50vw;
    margin-left: 5vw;
}

.educationDetails {
    padding-left: 4%;
    width: 100%;
}

@media screen and (max-width: 480px) {
    #educationContainer {
        padding-top: 10vh;
        padding-bottom: 10vh;
        background: none;
    }  

    .educationTable {
        width: 90vw;
    }

    #educationHeader {
        font-family: 'Courier New', Courier, monospace;
        font-size: 7vw;
    }

    .educationDetails h2 {
        font-size: 3.5vw;
    }

    .educationDetails h3 {
        font-size: 3vw;
    }

    .educationDetails h4 {
        font-size: 3vw;
    }

    .educationTable {
        margin-top: 5%;
        background: rgb(220, 241, 220);
        border-radius: 10px 50px 10px 50px; 
    }
}

